export let elementSchema = {
  type: 'string',
  optional: true,
  default: 'input',
  lowercase: true,
  enum: [
    'input',
    'textarea',
    'select',
    'div',
    'hr',
    'br',
    'h1',
    'h2',
    'h3',
    'h4',
    'h5',
    'h6',
  ],
};

export let inputTypeSchema = {
  type: 'string',
  optional: true,
  default: 'text',
  enum: [
    'button',
    'checkbox',
    'color',
    'date',
    'datetime-local',
    'email',
    'file',
    'hidden',
    'image',
    'month',
    'number',
    'password',
    'radio',
    'range',
    'reset',
    'search',
    'submit',
    'tel',
    'text',
    'time',
    'url',
    'week',
  ],
};

export const controlSchema = {
  $$root: true,
  //   $$strict: 'remove',

  type: 'object',
  props: {
    element: elementSchema,
    attributes: {
      type: 'object',
      props: {
        name: 'string',
        type: inputTypeSchema,
      },
    },
  },
};
